import {
  Component,
  OnInit,
} from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Cate } from '../models/cate.model';
import { Menu } from '../models/menu.model';
// for auth
import { AngularFireAuth } from 'angularfire2/auth';
// for database
import { AngularFireDatabase, AngularFireList, AngularFireObject } from 'angularfire2/database';
import { Observable } from 'rxjs/Observable';
import { fadeInAnimation } from './_animations/index';


@Component({
  selector: 'home',
  template: `    
      <div class="container">
        <div class="row">
          <div class="col-md-12 text-center marb-35">
            <h1 class="header-h">Menu List</h1>
            <!--<p class="header-p">Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diam nonummy-->
              <!--<br>nibh euismod tincidunt ut laoreet dolore magna aliquam. </p>-->
          </div>
          <div class="col-md-12  text-center gallery-trigger">
            <ul>
              <li><a class="filter" [routerLink]=" ['/home', 'Lunch Special'] ">Launch Specials</a></li>
              <li><a class="filter" [routerLink]=" ['/home', 'Appetizer'] ">Appetizers</a></li>
              <li><a class="filter" [routerLink]=" ['/home', 'Soup'] ">Soups</a></li>
              <li><a class="filter" [routerLink]=" ['/home', 'Thai Salad'] ">Thai Salad</a></li>
              <li><a class="filter" [routerLink]=" ['/home', 'Thai curries'] ">Thai curries</a></li>
              <li><a class="filter" [routerLink]=" ['/home', 'Stir-fried Dish'] ">Stir-fired Dishes</a></li>
              <li><a class="filter" [routerLink]=" ['/home', 'Fish & Seafood'] ">Seafood</a></li>
              <li><a class="filter" [routerLink]=" ['/home', 'Barbecue'] ">Barbecue</a></li>
              <li><a class="filter" [routerLink]=" ['/home', 'Vegetable'] ">Vegetable</a></li>
              <li><a class="filter" [routerLink]=" ['/home', 'Noodle Dish'] ">Noodle Dishes</a></li>
              <li><a class="filter" [routerLink]=" ['/home', 'Rice Dish'] ">Rice Dishes</a></li>
              <li><a class="filter" [routerLink]=" ['/home', 'Thai Dessert'] ">Thai Desserts</a></li>
            </ul>
          </div>
        </div>
      </div>
      <div id="Container" *ngFor="let menu of menulist">
        <div class="menu-restaurant col-xs-12 col-sm-6">
                        <span class="clearfix">
                        <a class="menu-title col-xs-11">{{menu.name}}</a>
                        <span style="left: 175px; right: 40px;" class="menu-line"></span>
                        <span class="menu-price">{{menu.oakville}}</span>
                      </span>
          <span class="menu-subtitle">{{menu.description}}</span>
        </div>
      </div>
  `,

  // // make slide in/out animation available to this component
  // animations: [fadeInAnimation],
  //
  // // attach the slide in/out animation to the host (root) element of this component
  // host: { '[@fadeInAnimation]': '' }
})
export class HomeComponent implements OnInit {

 // public cate: AngularFireList<any>;
  public catename: string  = "";

  public menulist: Menu[] = new Array<Menu>();
  //
  itemsRef: AngularFireList<any>;
  items: Observable<any[]>;


constructor(
    private route: ActivatedRoute,
    private router: Router,
    private db: AngularFireDatabase)
  {

   var subscriber = this.route.params
     .subscribe((params) =>{
       if (params['id']) {
          this.db.list('/allmenus', (ref) => ref.orderByChild('category').equalTo(params['id']))
           .valueChanges()
           .subscribe(data =>{
             this.menulist = this.buildMenu(data[0]['menus']);
           });
       }
     });

   // setTimeout(function () {
   //   subscriber.unsubscribe();
   //   console.log("unscription done!!");
   // },5000);

  }

  public ngOnInit() {}





  addItem(newName: string) {
    this.itemsRef.push({ text: newName });
  }
  updateItem(key: string, newText: string) {
    this.itemsRef.update(key, { text: newText });
  }
  deleteItem(key: string) {
    this.itemsRef.remove(key);
  }
  deleteEverything() {
    this.itemsRef.remove();
  }




  public buildMenu(menus):Menu[]{
    let menuArr: Menu[] = new Array<Menu>();
    menus.forEach(o =>{
      let menu = new Menu(o['burlington'],o['oakville'], o['thaismile'], o['description'], o['image'], o['name'], o['thainame'], o['spice_level']);
      menuArr.push(menu);
    });
    return menuArr;
  }

}



