
export class Menu{
  public burlington: Number;
  public oakville: Number;
  public thaismile: Number;
  public description: String;
  public image: String;
  public name: String;
  public spice: Number;
  public thainame: String;
  constructor(
    burlington:Number,
    oakville:Number,
    thaismile:Number,
    description:String,
    image:String,
    name:String,
    thainame:String,
    spice:Number
  ){
      this.burlington = burlington;
      this.oakville = oakville;
      this.thaismile = thaismile;
      this.description = description;
      this.image = image;
      this.name = name;
      this.thainame = thainame;
      this.spice = spice;
  }
}



