import {
  Component,
  OnInit,
} from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Cate } from '../models/cate.model';
import { Menu } from '../models/menu.model';
// for auth
import { AngularFireAuth } from 'angularfire2/auth';
// for database
import { AngularFireDatabase, AngularFireList, AngularFireObject } from 'angularfire2/database';
import { Observable } from 'rxjs/Observable';
import { fadeInAnimation } from './_animations/index';


@Component({
  selector: 'menulist',
  template: `    
      <div id="Container" *ngFor="let menu of menulist">
        <div class="menu-restaurant col-xs-12 col-sm-6">
                        <span class="clearfix">
                        <a class="menu-title" href="#">{{menu.name}}</a>
                        <span style="left: 175px; right: 40px;" class="menu-line"></span>
                        <span class="menu-price">{{menu.oakville}}</span>
                      </span>
          <span class="menu-subtitle">{{menu.description}}</span>
        </div>
      </div>
  `,

  // // make slide in/out animation available to this component
  // animations: [fadeInAnimation],
  //
  // // attach the slide in/out animation to the host (root) element of this component
  // host: { '[@fadeInAnimation]': '' }
})
export class MenulistComponent implements OnInit {

  // public cate: AngularFireList<any>;
  public catename: string  = "";

  public menulist: Menu[] = new Array<Menu>();
  //
  itemsRef: AngularFireList<any>;
  items: Observable<any[]>;


  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private db: AngularFireDatabase)
  {
  }

  public ngOnInit() {
    var subscriber = this.route.params
      .subscribe((params) =>{
        if (params['id']) {
          this.db.list('/allmenus', (ref) => ref.orderByChild('category').equalTo(params['id']))
            .valueChanges()
            .subscribe(data =>{
              this.menulist = this.buildMenu(data[0]['menus']);
            });
        }
      });
  }



  public buildMenu(menus):Menu[]{
    let menuArr: Menu[] = new Array<Menu>();
    menus.forEach(o =>{
      let menu = new Menu(o['burlington'],o['oakville'], o['thaismile'], o['description'], o['image'], o['name'], o['thainame'], o['spice_level']);
      menuArr.push(menu);
    });
    return menuArr;
  }

}



